<template>
  <div id="seller">
    <div class="nav-left">
      <a class="nav-left-item" href="/user/seller/goodDict">
        
      </a>
      <a class="nav-left-item" href="/user/seller/sellReport">
        
      </a>
      <!-- <a class="nav-left-item" href="/user/seller/orderLog">
        
      </a> -->
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
    }
  },
  // 1. 
  beforeCreate () {},
  // Vue
  created () {},
  methods: {
  },
  // el,template;
  beforeMount () {
  },
  // 
  mounted () {
  },
  // beforeUpdate updated
  beforeUpdate () {
    // 
    if (Number(window.sessionStorage.getItem('userType')) === 2) {
      this.$router.push({
        name: `user_seller_goodDict`
      })
    } else {
      alert(window.sessionStorage.getItem('userType'), '')
    }
  },
  updated () {
    // DOMDOM
  },
  beforeDestroy () {
    // 
    // 
  },
  destroyed () {
    // 
    // 
  }
}
</script>

<style scoped>
#seller{
  width: 100%;
}
.nav-left{
  position: absolute;
  z-index: 20;
  left: 0;
  top: 6rem;
  margin: 0rem 3rem 3rem 3rem;
  padding: 0 1rem 1rem 1rem;
  width: 10rem;
  height: 35rem;
  border: 1px solid #5c6a77;
  border-radius: .2rem;
  color: #2c3e50;
  background-color: white;
  opacity: 0.9;
}
.nav-left-item{
  position: relative;
  display: block;
  height: 4rem;
  width: 100%;
  line-height: 5rem;
  text-align: center;
  border-bottom: 2px solid #5c6a77;
  cursor: pointer;
  color: #2c3e50;
  text-decoration: none;
}
</style>



